<template>
  <div class="app-container" id="alarmRecord">
    <div class="topBox filter-container">
      <el-select v-model="listQuery.type" @change="changeType" class="filter-item" multiple collapse-tags>
        <el-checkbox class="selectAll" v-model="checked" @change='selectAll'>全选</el-checkbox>
        <el-option v-for="item in alarmTypeOptions" :key="item.value" :label="item.key" :value="item.value"></el-option>
      </el-select>
      <el-select v-model="listQuery.status" class="filter-item">
        <el-option v-for="item in alarmStatusOptions" :key="item.value" :label="item.key" :value="item.value"></el-option>
      </el-select>
      <el-date-picker class="filter-item" v-model="seachTime" type="date" value-format="yyyy_MM_d" placeholder="选择日期" :clearable="false"></el-date-picker>
      <el-cascader @change="removeValid()" v-model="listQuery.imeiCar" placeholder="按车辆搜索" :props="propsCar" :options="dataCar" :show-all-levels="false" clearable filterable collapse-tags>
      </el-cascader>
    
      <el-button type="primary" @click="getList" class="filter-item" icon="el-icon-search">搜索</el-button>
      <el-button type="primary" @click="exportDo" class="filter-item" icon="iconfont icondaochu" :loading="exportLoading">导出</el-button>
    </div>
   
    <div class="tableBox">
      <el-table :key="tableKey" v-loading="listLoading" :data="list" fit highlight-current-row style="width: 100%;" v-if="mediaNone" :row-class-name="tableRowClassName">
        <el-table-column label="序号" prop="id" sortable="custom" align="center" width="80" type="index" :index="indexMethod"></el-table-column>
        <el-table-column v-if="power==1" label="设备编号" min-width="80px" align="center">
          <template slot-scope="{row}">
            <span>{{ row. objectId}}</span>
          </template>
        </el-table-column>
        <el-table-column label="车牌号" min-width="80px" align="center">
          <template slot-scope="{row}">
            <span>{{ row.licenseNo}}</span>
          </template>
        </el-table-column>
      
        <el-table-column label="报警类型" min-width="120px" align="center">
          <template slot-scope="{row}">
            <span>{{ alarmObj[row. expType]}}</span>
          </template>
        </el-table-column>
        <el-table-column label="创建时间" min-width="120px" align="center">
          <template slot-scope="{row}">
            <span>{{ formatTime(row.createTime)}}</span>
          </template>
        </el-table-column>

        <el-table-column label="推迟时间" min-width="120px" align="center">
          <template slot-scope="{row}">
            <span>{{ formatTime(row.delayTime)}}</span>
          </template>
        </el-table-column>
        <el-table-column label="处理人" min-width="80px" align="center">
          <template slot-scope="{row}">
            <span>{{ row. handlerName}}</span>
          </template>
        </el-table-column>
        <el-table-column label="处理时间" min-width="120px" align="center">
          <template slot-scope="{row}">
            <span>{{ formatTime(row.handlerTime)}}</span>
          </template>
        </el-table-column>
        <el-table-column label="备注" min-width="80px" align="center">
          <template slot-scope="{row}">
            <span>{{ row. handlerInfo}}</span>
          </template>
        </el-table-column>
        <el-table-column label="报警状态" width="80px" align="center">
          <template slot-scope="{row}">
            <span :class="{'red':row.handler==0}">{{ alarmStatus(row.handler)}}</span>
          </template>
        </el-table-column>
        <el-table-column label="操作" fixed="right" align="center" min-width="250" class-name="small-padding fixed-width">
          <template slot-scope="{row}">
            <el-button :disabled="!((row.handler===0||row.handler===2))" type="primary" size="mini" @click="handleUpdate(row,'deal')" icon="iconfont iconchuli">处理</el-button>
            <!-- <el-button :disabled="!((row.handler===0||row.handler===2))" type="primary" size="mini" @click="dealAlarm(row)" icon="iconfont iconchuli">处理</el-button> -->

            <el-button type="primary" class="checkbtn" size="mini" @click="handleUpdate(row,'check')" :disabled="!(row.handler===0&&row.uuid)" icon="iconfont iconyanchi">延迟</el-button>
          </template>
        </el-table-column>
      </el-table>
    </div>
    <pagination :page-sizes="[10,10,30]"  v-show="total>0" :total="total" :page.sync="listQuery.page" :limit.sync="listQuery.limit" @pagination="getList" layout="prev, pager, next, sizes, total, jumper" />

    <el-dialog :class="{'delayStyle':(dialogStatus!=='deal')}" :title="textMap[dialogStatus]" :visible.sync="dialogFormVisible" center>
      <el-form ref="dataForm" :rules="rules" :model="temp" label-position="left">
        <el-form-item label="延迟时间(分钟)" prop="time" v-if="dialogStatus!=='deal'">
          <el-input v-model.number="temp.time" />
        </el-form-item>
        <el-form-item label="处理记录" prop="info" class="info" v-if="dialogStatus==='deal'">
          <el-input type="textarea" v-model="temp.info" :autosize="{ minRows: 6}"/>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button type="primary" @click="dialogStatus==='deal'?dealData():delayData()">确定</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
function debounce(func, wait = 1000) {
  let timeout;
  return function (event) {
    clearTimeout(timeout)
    timeout = setTimeout(() => {
      func.call(this, event)
    }, wait)
  }
}
import Pagination from "@/components/Pagination"; // secondary package based on el-pagination
//报警列表 延迟报警 报警处理 查询设备最近上线时间 设备报警列表
import {
  alarmListDay,
  alarmDelay,
  alarmHandler,
  exportBigExcel,
} from "../../../api/alarmList";
import {
  teamAndCarAllBindList,
} from "../../../api/realTimeMonitoring";
import { formatDate, renderTime } from "../../../../src/common"; //格式化时间

export default {
  name: "alarmRcord",
  components: { Pagination },
  data() {
    return {
      power: JSON.parse(sessionStorage.getItem("userInfo")).Power, // 权限
      tableKey: 0,
      list: [], //报警记录列表
      total: 0, //报警记录总条数
      listLoading: false,
      listQuery: {
        page: 1,
        limit: 10,
        type: [],
        status: -1,
        type1: [400, 401, 402, 403, 404, 618, 619, 620, 621, 622, 623, 624, 625, 626, 627, 630, 631],
        keyword: "", //模糊查询设备号
        imeiCar: [],
        imeiGun: []
      },
      exportData: {
        expStatus: null, //异常状态
        tableName: "", //表名，格式yyyy_MM
        days: "", //哪一天
      }, //导出需要的数据
      indexMethod: 1, //序号
      temp: {}, //处理报警和延迟报警提交内容
      dialogFormVisible: false, //弹窗显隐
      dialogStatus: "", //延迟或处理
      textMap: {
        deal: "处理",
        check: "延迟"
      },
      rules: {
        time: [{ required: true, message: "请填写延迟时间", trigger: "blur" }],
        info: [{ required: true, message: "请填写处理意见", trigger: "blur" }]
      }, //表单校验
      //报警类型对象
      alarmTypeOptions: [
        { key: "车辆信号丢失", value: 5 },
        { key: "自定义的超速", value: 11 },
        { key: "一键报警", value: 12 },
        { key: "车载定位系统推送的超速", value: 13 },
        { key: "摄像头损坏", value: 15 },
        { key: "摄像头无信号", value: 16 },
      ],

      adasDmsaAlarmTypeOptions: [
        { key: "前车碰撞预警", value: 400 },
        { key: "道路偏离预警", value: 401 },
        { key: "行人检测预警", value: 402 },
        { key: "驾驶员遮挡或镜头偏离位置", value: 403 },
        // { key: "不系安全带", value: 404 },
        { key: "疲劳驾驶报警1级", value: 618 },
        { key: "疲劳驾驶报警2级", value: 619 },
        { key: "接打电话报警1级", value: 620 },
        { key: "接打电话报警2级", value: 621 },
        { key: "抽烟报警1级", value: 622 },
        { key: "抽烟报警2级", value: 623 },
        { key: "分神驾驶报警1级", value: 624 },
        { key: "分神驾驶报警2级", value: 625 },
        { key: "驾驶员异常报警1级", value: 626 },
        { key: "驾驶员异常报警2级", value: 627 },
        // { key: "驾驶员变更事件1级", value: 630 },
        // { key: "驾驶员变更事件2级", value: 631 },
      ],
      alarmObj: {
        1: '枪支偏离车辆',
        2: '车辆偏移路线',
        3: '拆卸报警',
        4: '枪支网络异常',
        5: '车辆信号丢失',
        6: '同一辆车周围少于2支枪',
        7: '未到规定时间内打卡',
        9: '车辆道路变换',
        10: '电量不足',
        11: '自定义的超速',
        12: '一键报警',
        13: '车载定位系统推送的超速',
        14: '未保留笼统的异常',
        15: '摄像头损坏',
        16: '摄像头无信号',
        //
        400: '前车碰撞预警',
        401: '道路偏离预警',
        402: '行人检测预警',
        403: '驾驶员遮挡或镜头偏离位置',
        404: '不系安全带',
        618: '疲劳驾驶报警1级',
        619: '疲劳驾驶报警2级',
        620: '接打电话报警1级',
        621: '接打电话报警2级',
        622: '抽烟报警1级',
        623: '抽烟报警2级',
        624: '分神驾驶报警1级',
        625: '分神驾驶报警2级',
        626: '驾驶员异常报警1级',
        627: '驾驶员异常报警2级',
        630: '驾驶员变更事件1级',
        631: '驾驶员变更事件2级',
        //
        701: '枪支超时未归还报警',
        711: '款箱数量异常报警'
      },
      //异常处理状态对象
      alarmStatusOptions: [
        { key: "所有异常", value: -1 },
        { key: "未处理异常", value: 0 },
        { key: "忽略异常", value: 1 },
        { key: "延迟处理异常", value: 2 },
        { key: "已处理异常", value: 3 }
      ],
      seachTime: this.formatDateTime(new Date()), //初始化搜索时间
      seachTime1: this.formatDateTime(new Date()), //初始化搜索时间
      carOrGun: null, //表头显示的‘枪号’或‘车牌号’或‘车牌号或枪号’（有的报警类型是即有可能包括车又有可能包括枪）
      onlineTime: null, //最近上线时间

      propsCar: {
        expandTrigger: "hover",
        multiple: true
      },
      propsGun: {
        expandTrigger: "hover",
        multiple: true
      },
      dataCar: [], // 多设备车
      dataGun: [], // 多设备枪,
      imei2name: {}, // 设备的imei和对应的名字
      gunAlarmType: [1, 3, 4, 10, 12],
      carAlarmType: [2, 5, 6, 7, 9, 11, 13, 14, 15, 16, 17],
      tableBoxType: 'NOADAS',
      checked: true,
      selectedArray: [],
      exportLoading: false

    };
  },
  props: ["mediaNone"], // 兼容小屏（小屏时显示为用于拆卸报警的最近上线时间）

  created() {

  
      this.selectAll();
      let p1 = this.getTeamAndCarList();
      p1.then(() => {

        this.getList();
      })

   
  },
 
  methods: {
    selectAll() {
      this.listQuery.type = []
      if (this.checked) {
        this.alarmTypeOptions.map((item) => {
          this.listQuery.type.push(item.value)
        })
      } else {
        this.listQuery.type = []
      }
    },
    
   
    //用于判断报警类型应该显示的是枪支列表还是车辆列表
    changeType(val) {
    

      if (val.length === this.alarmTypeOptions.length) {
        this.checked = true
      } else {
        this.checked = false
      }

    }, //获取设备最近上线时间
    removeValid() {
      // 选择设备后移除验证
      // this.$refs.mdForm.clearValidate(["imeiCar", "imeiGun"]);
    },
    //获取左侧大队和车辆tree请求
    getTeamAndCarList() {
      let that = this;
      return new Promise((resolve, reject) => {
        teamAndCarAllBindList({}).then(res => {
          if (res.status == 0) {
            that.dataCar = this.formatCas(res.data, "serialNo", "licenseNo"); // 设置车下拉树的数据格式
            //查看证据界面点击返回携带的设备，在报警记录界面默认选择
            if (this.$route.params && this.$route.params.imeiCar) {
              that.listQuery.imeiCar = this.$route.params.imeiCar;
              that.listQuery.type = [17]
            } else {
              for (let i = 0; i < this.dataCar.length; i++) {
                for (let j = 0; j < this.dataCar[i].children.length; j++) {
                  that.listQuery.imeiCar.push([that.dataCar[i].value, this.dataCar[i].children[j].value]);
                }
              }
            }
            resolve();
          } else {
            this.$message.error(res.msg);
          }
        });
      })

    },
    
    formatCas(data, imei, name) {

      // 格式化级联选择器数据 data是原始数据 imei是vaue值 name是label值
      let result = [],
        id = 0,
        _t = this;
      for (let i in data) {
        let childs = []; // 格式化子数组
        if (i == "报废车辆") { continue }
        if (i == "报废枪支") { continue }

        data[i].forEach(x => {
          //过滤掉没有绑定设备的车
          if (x[imei] !== "") {

            childs.push({
              value: x[imei],
              label: x[name],
            });
            _t.imei2name[x[imei]] = x[name]; // 将设备号与设备名组成一个数组
          }
        });
        result.push({
          value: `i${id++}`,
          label: i == "" ? "未分组" : i,
          children: childs
        });
      }
      return result;

    },
   
    exportDo: debounce(function (e) {
      this.exportExcel();
    }),
    //导出
    exportExcel() {
      this.exportLoading = true;
      // this.$message.warning("稍等,数据量较大,请不要重复点击哦");
      this.exportData.tableName = this.seachTime.split("_")[0] + "_" + this.seachTime.split("_")[1];
      let date = this.seachTime, arr = [], obj = {};
      this.exportData.expKeywords = []

      this.exportData.days = this.seachTime.split("_")[2];
      this.exportData.expTypes = [...this.listQuery.type];

      let index = this.exportData.expTypes.indexOf(17);
      if (index !== -1) {
        this.exportData.expTypes.splice(index, 1);
        this.exportData.expTypes = this.exportData.expTypes.concat(this.listQuery.type1)
      }
     
      arr = this.listQuery.imeiGun.concat(this.listQuery.imeiCar)
      for (let i = 0; i < arr.length; i++) {
        this.exportData.expKeywords.push(arr[i][1])
      }
      this.exportData.expStatus = this.listQuery.status;
      // this.exportData.expKeywords = this.listQuery.keyword;
      let type = this.exportData.expType;
      let status = this.exportData.expStatus;
      // let keyword = this.exportData.keyword
      // console.warn(this.exportData)

      // return;
      obj = { ...this.exportData }
      obj.excelType = '1001'
     
      obj.positive = false;
      obj.title = '异常表导出';
      obj.expKeywords = (this.exportData.expKeywords.length ? this.exportData.expKeywords : null)

      exportBigExcel(obj).then((response, a, b) => {
        try {
          let enc = new TextDecoder('utf-8')
          let data = JSON.parse(enc.decode(new Uint8Array(response)))
          this.$message({
            message: data.msg,
            type: 'warning'
          });
          // if (response.msg) {
          //   this.$message({
          //     message: '操作失败',
          //     type: 'warning'
          //   });
          // }

        } catch (error) {
          let blob = new Blob([response], { type: "application/excel" }); // 假设文件为pdf
          let link = document.createElement("a");
          let typeText, statusText;
          for (let i = 0; i < this.alarmTypeOptions.length; i++) {
            if (this.listQuery.type == this.alarmTypeOptions[i].value) {


              typeText = this.alarmTypeOptions[i].key

              
              break;
            }
          }
          for (let j = 0; j < this.alarmStatusOptions.length; j++) {
            if (status == this.alarmStatusOptions[j].value) {
              statusText = this.alarmStatusOptions[j].key;
              break;
            }
          }
          let fileName;
          fileName = date + "----" + "----" + statusText + "----" + "报警记录" + ".xlsx";

          // if (obj.expKeywords) {
          //   fileName = obj.expKeywords.toString() + "----" + date + "----" + typeText + "----" + statusText + "----" + "报警记录" + ".xlsx";
          // } else {
          //   fileName = date + "----" + typeText + "----" + statusText + "----" + "报警记录" + ".xlsx";
          // }

          link.href = window.URL.createObjectURL(blob);
          link.download = fileName;
          link.click();
          link.remove();
        }

      }).finally(() => {
        this.exportLoading = false;
      });
    },
    //格式化时间
    formatDateTime(date) {
      var y = date.getFullYear();
      var m = date.getMonth() + 1;
      m = m < 10 ? "0" + m : m;
      var d = date.getDate();
      // d = d < 10 ? "0" + d : d;
      var h = date.getHours();
      var minute = date.getMinutes();
      minute = minute < 10 ? "0" + minute : minute;
      return y + "_" + m + "_" + d;
    },
    //报警记录列表
    getList() {
      this.seachTime1 = this.seachTime;
      this.listLoading = true;
      this.listQuery.tableName =
        this.seachTime.split("_")[0] + "_" + this.seachTime.split("_")[1];
      this.listQuery.days = this.seachTime.split("_")[2];
      let arr = [], imeis = [], obj = {}, req = null;
      arr = this.listQuery.imeiCar.concat(this.listQuery.imeiGun)
      for (let i = 0; i < arr.length; i++) {
        imeis.push(arr[i][1])
      }
      obj.page = this.listQuery.page;
      obj.limit = this.listQuery.limit;

    

      obj.types = [...this.listQuery.type];

      let index = obj.types.indexOf(17);
      if (index !== -1) {
        obj.types.splice(index, 1);
        obj.types = obj.types.concat(this.listQuery.type1)
      }
      obj.status = this.listQuery.status;
      obj.tableName = this.listQuery.tableName;
      obj.days = this.listQuery.days;
      obj.positive = false;
      if (arr.length) {
        obj.keywords = imeis;
      }
      req = alarmListDay;

      req(obj).then(response => {
        this.list = response.data ? response.data.records : [];
        this.total = response.data.total;
       
        this.indexMethod = (this.listQuery.page - 1) * this.listQuery.limit + 1;
       
        this.listLoading = false;
      }).finally(() => {
        this.listLoading = false;
      });
    },




    //重置
    resetTemp() {
      this.temp = {
        uuid: null,
        info: null
      };
    },
    //处理报警
    dealData() {
      var that = this;
      this.$refs["dataForm"].validate(valid => {
        if (valid) {
          let subData = {}
          if (this.temp.uuid) {
        subData.uuidMap = {[this.temp.rowTime]:[this.temp.uuid]};
      } else {
        subData.idMap = {[this.temp.rowTime]:[this.temp.id]};
      }
      subData.handlerInfo= this.temp.info 
          alarmHandler(subData).then(() => {
            that.getList();
            that.dialogFormVisible = false;
            that.$notify({
              title: "Success",
              message: "处理成功",
              type: "success",
              duration: 2000
            });
          });
          this.dialogFormVisible = false;
        }
      });
    },
    //点击处理或延迟按钮
    handleUpdate(row, type) {
      this.resetTemp();
      this.$nextTick(() => {
        this.$refs["dataForm"].clearValidate();
      });
      //点击处理报警
      this.temp.uuid = row.uuid;
      this.temp.id=row.id;
      this.temp.rowTime=this.formatTime(row.createTime).split(' ')[0].replace(/-/g,'_')
      if (type == "deal") {
        this.dialogStatus = "deal";
      } else {
        //点击延迟报警
        this.temp.uuid = row.uuid;
        this.dialogStatus = "check";
      }

      this.dialogFormVisible = true;
    },
    //延迟报警
    delayData() {
      this.$refs["dataForm"].validate(valid => {
        if (valid) {
          alarmDelay({ uuids: [this.temp.uuid], time: this.temp.time }).then(
            () => {
              this.getList();
              this.dialogFormVisible = false;
              this.$notify({
                title: "Success",
                message: "Update Successfully",
                type: "success",
                duration: 2000
              });
            }
          );
          this.dialogFormVisible = false;
        }
      });
    },
    formatTime1(time) {
      return formatDate(time, 1);
    },

    formatTime(time) {
      return renderTime(time);
    },
    //显示报警状态
    alarmStatus(status) {
      if (status == 0) {
        return "未处理";
      } else if (status == 1) {
        return "忽略";
      } else if (status == 2) {
        return "延迟处理";
      } else if (status == 3) {
        return "处理完成";
      }
    },
    tableRowClassName({ row, rowIndex }) {
      if (rowIndex % 2 == 0) {
        return "color-row";
      } else if (rowIndex % 2 == 1) {
        return "noColor-row";
      }

      return "";
    }
  }
};
</script>
